    saveAddDialog(formName) {
      let _this = this;
      _this.$refs[formName].validate(valid => {
        var user = {};
        for (var item in _this.addForm) {
          if (_this.addForm[item] !== "" && item !== "visible") {
            user[item] = _this.addForm[item];
          }
          if (item == "deadline") {
            user[item] = _this.expirationDate;
          }
        }
        if (user.deadline == undefined) {
          user.deadline = null;
        }
        if (_this.addForm.entId == "") {
          _this.$message("请选择组织机构");
          return;
        }
        // if(this.userType==null||this.userType==""||this.userType==undefined){
        //   this.$message("请选择用户类型");
        //     return;
        // };
        // if (
        //   _this.addForm.type == null ||
        //   _this.addForm.type == "" ||
        //   _this.addForm.type == undefined
        // ) {
        //   _this.$message("请选择用户类型");
        //   return;
        // }
        for (var item in _this.additionalForm) {
          if (_this.additionalForm[item] == "") {
            _this.additionalForm[item] = null;
          }
        }
        var obj = {};
        obj = extend({}, user, _this.additionalForm);
        if (valid) {
          _this.$ajax
            .post("rest/user", obj)
            .then(response => {
              _this.$message.success(response.data.answerMsg);
              if (response.data.status) {
                _this.requesetUsers();
                _this.isShowAddDialog = false;
              }
            })
            .catch(error => {
              console.log(error);
            });
        } else {
          return false;
        }
      });
