    saveEditDialog() {
      //   if (this.editForm.type == "" || this.editForm.type == null) {
      //     this.$message("请选择用户类型");
      //     return;
      //   }
      var obj = {};
      for (var item in this.editForm) {
        if (
          item == "departmentName" ||
          item == "orgName" ||
          item == "sex" ||
          item == ""
        ) {
          delete this.editForm[item];
        } else if (this.editForm[item] == undefined) {
          delete this.editForm[item];
        }
      }
      for (var item in this.additionalEditForm) {
        if (this.additionalEditForm[item] == "") {
          this.additionalEditForm[item] = null;
        }
      }
      obj = extend({}, this.editForm, this.additionalEditForm);
      this.$ajax({
        method: "put",
        url: "rest/user",
        data: obj
      })
        .then(res => {
          if (res.data.status) {
            this.$message({
              message: res.data.answerMsg,
              type: "success"
            });
            this.isShowEditDialog = false;
            this.requesetUsers();
          } else {
            this.$message.error(res.data.answerMsg);
          }
        })
        .catch(err => {
          console.log(err, "编辑错误");
        });
