    resetPassword(formName) {
      let _this = this;
      let data = {};
      data.accountList = [];
      this.resetTable.forEach(function(value, index) {
        data.accountList[index] = value.entId + "," + value.account;
      });
      data.accountList = data.accountList;
      if (this.resetForm.method == 0) {
        this.$refs[formName].validate(valid => {
          if (valid) {
            data.pwd = this.resetForm.password;
            this.$ajax
              .post("rest/user/pwd", data)
              .then(res => {
                this.$message(res.data.answerMsg);
                this.resetForm = {
                  method: "",
                  password: ""
                };
                this.isShowResetDialog = false;
              })
              .catch(function(err) {
                console.log(err);
              });
          } else {
            return false;
          }
        });
      } else if (this.resetForm.method == 1) {
        data.pwd = "";
        this.$ajax
          .post("rest/user/pwd", data)
          .then(res => {
            this.$message(res.data.answerMsg);
            this.resetForm = {
              method: "",
              password: ""
            };
            this.isShowResetDialog = false;
          })
          .catch(function(err) {
            console.log(err);
          });
      } else {
        return false;
      }
    },
    //用户授权
    showEmpowerDialog() {
      this.checkedRoles = [];
      if (!this.resetTable.length) {
        //todo 提示没有选中
        this.$message("没有选中用户");
      } else if (this.resetTable.length > 1) {
        this.$message("无法对多个用户进行授权");
      } else {
        this.isShowEmpowerDialog = true;
        var resetTableData = this.resetTable;
        var userId = resetTableData[0].id;
        this.$ajax
          .get("rest/user/roleUser", {
            params: {
              userId: userId
            }
          })
          .then(res => {
            this.roleData = res.data.records;
            for (var i = 0; i < res.data.records.length; i++) {
              if (res.data.records[i].checked == 1) {
                this.checkedRoles.push(res.data.records[i].roleId);
                this.deleteList.push(res.data.records[i].roleId);
                this.deleteListArray.push(res.data.records[i]);
              }
            }
          })
          .catch(function(err) {
            console.log(err);
          });
      }
    },

    saveEmpowerDialog() {
      var resetTableData = this.resetTable;
      var userId = resetTableData[0].id;
      var roleUserObj = {};
      let list = difference(this.deleteList, this.checkedRoles);
      let me = this;
      function getList(arr = [], deleteUser) {
        //获取对象数组
        return arr.map((item, index) => {
          let obj = {};
          obj.userId = userId;
          obj.roleId = item;
          if (deleteUser !== undefined) {
            for (let i = 0; i < me.deleteListArray.length; i++) {
              //获取对应的roleuserid
              if (me.deleteListArray[i].roleId === obj.roleId) {
                obj.id =
                  me.deleteListArray[i].id === undefined
                    ? ""
                    : me.deleteListArray[i].id;
              }
            }
          }
          return obj;
        });
      }
      var saveList = getList(list.saveList);
      var deleteList = getList(list.deleteList, "deleteUser");
      roleUserObj = { deleteList: deleteList, saveList: saveList };
      this.$ajax
        .post("rest/user/roleUser", roleUserObj)
        .then(res => {
          this.$message.success(res.data.answerMsg);
          this.isShowEmpowerDialog = false;
        })
        .catch(err => {
          this.$message(err);
        });
    },
    closeAddDialog(formName1, formName2) {
      this.valueP = " ";
      //   this.$invokeSubMethod("setText", "oganizationTree", "");
      this.$invokeSubMethod("setText", "OrgAddTree", "");
      this.$refs[formName1].resetFields();
      this.$refs[formName2].resetFields();
      //   this.$invokeSubMethod("setText", "industryTree", "");
      this.nowTitleIndex = 0;
      //   this.addForm.type = "";
    },
    closeEditDialog(formName1, formName2) {
      //   this.$invokeSubMethod("setText", "editTree", "");
      this.$invokeSubMethod("setText", "OrgEditTree", "");
      //   this.$invokeSubMethod("setText", "industryEditTree", "");
      this.$refs[formName1].resetFields();
      this.$refs[formName2].resetFields();
      this.nowTitleIndex = 0;
    },
    closeResetDialog(formName) {
      this.resetForm.method = 0;
      this.$refs[formName].resetFields();
    },
    closeEmpowerDialog(formName) {
      // this.showUserEmpower=[];
      this.$refs[formName].resetFields();
    }
