  },
  created() {},
  mounted() {
    this.requesetUsers();
    this.requesetOrgListsTree();
    this.requestAllRoleData();
  }
};
</script>

<style>
.userOperation_div_wrap {
  position: relative;
}
/*头部的样式*/

.userOperation_span_title {
  float: right;
}

.userMangage_div_reset {
  text-align: center;
}

.userMangage_div_page {
  margin-top: 10px;
  text-align: right;
}

.userOperation_span_title .el-form-item__label {
  padding: 10px 8px 7px 0 !important;
}

.userOperation_img {
  position: absolute;
  right: 8px;
  top: 15px;
  width: 20px;
  cursor: pointer;
}

.userOperation_span_error {
  color: red;
  position: absolute;
  min-width: 120px;
}

.userOperation_btn {
  width: 90px;
  height: 30px;
  background-color: #f5f6f8 !important;
  border: 1px solid #dadada;
}

.userOperation_btn:hover,
.userOperation_btn:focus,
.userOperation_btn:active,
.userOperation_btn:visited {
  background-color: white !important;
  color: #1296db;
}

.userOperation_btn_search {
  background-color: white !important;
  color: #1296db;
  border-color: #20a0ff;
}

.userOperation_div_left {
  width: 286px;
  border: 1px solid lightgray;
  padding-top: 20px;
  height: 600px;
  padding-left: 9px;
}

.userOperation_div_left .el-form-item__content {
  width: 150px;
  display: inline-block;
  /* height: 25px; */
}

.userOperation_div_left .el-form-item__label {
  min-width: 105px;
  font-size: 14px;
  font-weight: bold;
}

.userOperation_div_left .base-selectTree-tree {
  z-index: 3;
}

.userOperation_dialog_edit .base-selectTree {
  z-index: inherit;
}

.userOperation_dialog_edit .base-selectTree-tree {
  z-index: 3;
  width: 240px;
}

.userOperation_tb {
  position: absolute;
  right: 0;
  left: 300px;
  top: 50px;
}

.userOperation_button_add {
  margin-left: 10px !important;
}

/* .userOperation_btn_reset {
  margin-left: 122px;
} */

.userOperation_item_center {
  text-align: center;
}

.userOperation_div_left .base-tree {
  overflow: auto;
  height: auto;
  margin-top: -10px;
  border: none !important;
  padding-right: 10px;
}

.userOperation_dialog_add .base-selectTree {
  z-index: inherit;
}

.userOperation_dialog_add .el-tree {
  z-index: 1;
}

.userOperation_checkbox {
  min-width: 160px;
  margin-left: 0px !important;
}

.userOperation_checkbox_group {
  margin: 0 auto;
  width: 335px;
}

.userOperation_a_leftLists {
  display: inline-block;
  cursor: pointer;
  line-height: 20px;
  background-color: #017dd4;
  min-width: 240px;
  color: white;
}

.userOperation_a_Lists {
  display: inline-block;
  cursor: pointer;
  line-height: 20px;
  min-width: 240px;
}

.userOperation_div_roleLists {
  padding-left: 10px;
  font-size: 15px;
}

.userOperation_div_roleLists > a {
  margin-bottom: 3px;
  padding: 3px;
}

.userOperation_div_roleLists > a:hover {
  background-color: #43a7ed;
  color: #fff;
}

.userOperation_div_menu {
  border: 1px solid #f4f4f4;
  height: 30px;
  background-color: #f4f4f4;
  margin-bottom: 10px;
  margin-top: -15px;
  width: 500px;
  margin-left: -20px;
}

.userOperation_div_menu > a {
  font-size: 14px;
  line-height: 30px;
  margin-left: 10px;
  cursor: pointer;
}

.userOperation_div_message {
  color: #65aaf0;
  border-bottom: 3px solid #65aaf0;
  padding-bottom: 3px;
}

.userOperation_div_radio .el-radio {
  min-width: 120px;
  line-height: 28px;
}

.userOperation_div_radio .el-radio + .el-radio {
  margin-left: 0px !important;
}

.userOperation_div_line {
  border-right: 1px solid #999;
  height: 15px;
  position: absolute;
  left: 102px;
  top: 51px;
}
.orgManagement_tree_error {
  position: absolute;
  left: -74px;
  top: 3px;
  color: red;
}
.userOperation_span_error {
  position: absolute;
  left: -106px;
  top: 3px;
  color: red;
}
.userOperation_span_error_1 {
  position: absolute;
  left: -78px;
  top: 2px;
  color: red;
}
</style>



